<template>
  <div>
    <button v-on="{ mousedown: doThis, mouseup: doThat }">
      객체로 이벤트 리스너 추가
    </button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      doThis: function () {
        console.log("마우스가 눌렸어요.");
      },
      doThat: function () {
        console.log("마우스에서 손을 땠어요.");
      },
    };
  },
};
</script>
<style></style>
